
.app-container{
    width: 100%;
    display: grid;
    grid-template-rows: minmax(50px, 80px) auto 50px;
    grid-template-columns: 1fr 5fr 1fr;
    min-height: 100vh;
    z-index: 2;
}
.header{
    grid-column: 2/3;
    grid-row: 1/2;
}
.main{
    grid-column: 1/4;
    grid-row: 2/3;
}
.footer{
    grid-column: 2/3;
    grid-row: 3/4;
}


