@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap');

.sidebar{
    display: none;
    position: absolute;
    width: 200px;
    height: auto;
    top: 50px;
    right: 0;
    background-image: linear-gradient(var(--color-koyu-mavi), #11348a);
   -webkit-transition:  .5s linear;
    -moz-transition:  .5s linear;
    -o-transition:  .5s linear;
    -ms-transition:  .5s linear;
    transition: .5s linear;
    z-index: 202;
    .side-nav{
        list-style: none;
        padding: 0;
        .side-item{
            margin:1em;
            width: auto;
            height: auto;
            display: flex;
            justify-content: center;
            
            .side-link{
                text-decoration: none;
                font-family: 'Montserrat Alternates', sans-serif;
                font-weight: 500;
                font-size: 1em;
                color: var(--color-turuncu);
                text-shadow: 0 0 25px var(--color-mavi), 0 0 50px var(--color-mavi),  0 0 75px var(--color-mavi),  0 0 100px var(--color-mavi);
                cursor: pointer;
                padding: 1em 0;
                width: 100%;
                display: flex;
                justify-content: center;
            }
        }
        .selectedSidebarItem{
            background: var(--color-turuncu);
            background: radial-gradient(circle, rgba(255,140,0,1) 0%, rgba(255, 140, 0, 0.7) 100%);
            border-radius: .25em;
            box-shadow: 0 0 3em .5em var(--color-mavi);
            .side-link{
                color: rgb(77, 43, 2) ;
            }
        }
    }
}
@media only screen and (max-width:768px) {
    .sidebar{
        display: block;
    }
    
}