.how-to-do-card{
    height: 376px;
    position: relative;
    border: 2px solid var(--color-pembe);
    border-radius: .5em;
    z-index: 1;

    .how-to-do-card-image{
        margin: 0;
        padding: 0;
        height: 100%;
        width: 100%;
        background-image: var(--bg-img);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        border-radius: 6px;
        -webkit-filter: blur(.2em);
        -moz-filter: blur(.2em);
        -o-filter: blur(.2em);
        -ms-filter: blur(.2em);
        filter: blur(.2em);
    }
    .how-to-do-card-title{
        overflow: hidden;
        box-sizing: border-box;
        color: var(--color-beyaz);
        font-family: var(--font-ma);
        font-size: 18px;
        font-weight: 500;
        background: linear-gradient(0deg, rgba(247,37,133,.7) 0%, rgba(247,37,133,0.05) 100%);;
        width: 100.5%;
        height: 144px;
        border-radius: 0 0 6px 6px;
        position: absolute;
        bottom: -1px;
        left: -1px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: .5em 1em;
    }
    .how-to-do-card-title-icon-container{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: auto;
            .how-to-do-card-title-icon{
                color: var(--color-beyaz);
                margin: .4em;
                background-color: var(--color-pembe);
                padding: 4px;
                border-radius: 4px;
            }
        }
}


.how-to-do-card:hover{
    transform: scale(1.05, 1.05);
    .how-to-do-card-image{
        -webkit-filter: blur(0px);
        -moz-filter: blur(0px);
        -o-filter: blur(0px);
        -ms-filter: blur(0px);
        filter: blur(0px);
    }
}

@media screen and (max-width:414px) {
    .how-to-do-card{
        .how-to-do-card-title{
            align-items: flex-start;
        }
    }
}