.add-post-container{
    width: 100%;
    height: auto;
    min-height: 80vh;
    background-color: var(--color-koyu-mavi);
    color: var(--color-siyah);
    margin-top: 20px;
    display: flex;
    font-size: 16px;
    .post-view, .add-post{
        margin: 1%;
        background-color: var(--color-beyaz);
    }
    .post-view{
        width: 34%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .add-post{
        width: 64%;
        .add-post-area{
            box-sizing: border-box;
            width: 100%;
            height: auto;
            border-top: 2px solid var(--color-koyu-mavi);
            margin-bottom: 1em;
            padding-left: 1em;
            display: flex;
            align-items: center;
            justify-content: center;
            padding-top: .8em;
            font-size: 20px; 
            textarea{
                font-size: 20px;
                width: 79%;
            }
            button{
                width: 15%;
                height: 50px;
                margin-left: 2%;
                background-color: var(--color-yesil);
            }
            input{
               font-size: 20px; 
               margin-left:1em;
            }
            .asdsaf{
                display: flex;
                flex-direction: column;
                div{
                    display: flex;
                    textarea{
                        width: 100%;
                    }
                }
            }
        }
    }
}

.allPosts{
    background-color: var(--color-kirmizi);
    padding: 2em 6em;
    margin: 2em 0;
    .allPostContainer{
        .allPostsIc{
            background-color: var(--color-siyah);
            display: flex;
            justify-content: space-between;
            border: 1px solid gray;
            .allPostsIcP{
                padding: 0 2em;
            }
            .allPostsIcBtn{
                padding: 0 2em;
                margin: .5em 2em;
            }
        }
        
    }
    
}