.post-content-container{
    width: 70%;
    height: auto;
    margin-top: 50px;
    
        .post-title{
            font-family: var(--font-ma);
            font-weight: 900;
            font-size: 40px;
            color: var(--color-siyah);
            letter-spacing: 2px;
            margin: 1em;
            text-align: center;
            text-shadow: 2px 0 0 var(--color-turuncu), -2px 0 0 var(--color-turuncu), 0 2px 0 var(--color-turuncu), 0 -2px 0 var(--color-turuncu), 1px 1px var(--color-turuncu), -1px -1px 0 var(--color-turuncu), 1px -1px 0 var(--color-turuncu), -1px 1px 0 var(--color-turuncu);
        }
    
}

@media screen and (max-width:992px) {
    .post-content-container{
        width: 80%;
            .post-title{
                font-weight: 900;
                font-size: 28px;
                margin:2em;
            }
        
    }
}
@media screen and (max-width:768px) {
    .post-content-container{
        width: 90%;
            .post-title{
                font-weight: 700;
                font-size: 24px;
                margin:2em;
            }
        
    }
}
@media screen and (max-width:414px) {
    .post-content-container{
        width: 100%;
            .post-title{
                font-weight: 700;
                font-size: 20px;
                margin:2em;
            }
        
    }
}

.post-content-inset-container{
    background-color: var(--color-koyu-mavi);
    padding: 2em;
    margin: .5em 0;
    border-radius: .5em;
}
@media screen and (max-width:414px) {
    .post-content-inset-container{
        padding: 1em;
    }
}
.p{
    font-family: Arial, Helvetica, sans-serif;
    text-indent:1em;
    margin: 1em 0;
    span{
        color: var(--color-turuncu);
        margin: 0;
        display: inline;
    }
}
.p-link{
    color: var(--color-turuncu);
    cursor: pointer;
    font-style: italic;
    text-decoration: none;
    display: inline;
}
.p-link:hover{
    color: var(--color-mavi);
}
.p-size-sm{
    font-size: 14px;
    &::first-letter{
        font-size: 1.1rem;
    }
}
.p-size-md{
    font-size: 16px;
    &::first-letter{
        font-size: 1.15rem;
    }
}
.p-size-lg{
    font-size: 18px;
    &::first-letter{
        font-size: 1.3rem;
    }
}
.p-size-xl{
    font-size: 24px;
    &::first-letter{
        font-size: 1.6rem;
    }
}

.p-midle{
    box-sizing: border-box;
    width: 100%;
    padding: 0 25%;
    text-align: center;
}


@media screen and (max-width:600px) {
    .p-midle{
        padding: 0 15%;
    }
}
@media screen and (max-width:414px) {
    .p-midle{
        padding: 0 10%;
    }
}


.post-space{
    width: 100%;
    height: 2em;
}
.p-sub-title{
    display: block;
    position: relative;
}
.p-sub-title::before{
    content: "// ";
    color: var(--color-turuncu);
    font-weight: bold;
}



