

.anim-container{
    width: 100%;
    height: auto;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .anim-loader{
        width: var(--width);
        height: var(--width);
        margin: -webkit-calc(var(--width)/4);
        margin: -moz-calc(var(--width)/4);
        margin: -o-calc(var(--width)/4);
        margin: -ms-calc(var(--width)/4);
        margin: calc(var(--width)/4);
        position: relative;
        span{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            -webkit-transform: rotate(-webkit-calc(18deg*var(--i)));
            -moz-transform: rotate(-moz-calc(18deg*var(--i)));
            -o-transform: rotate(-o-calc(18deg*var(--i)));
            -ms-transform: rotate(-ms-calc(18deg*var(--i)));
            transform: rotate(calc(18deg*var(--i)));
        }
        span::after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: -webkit-calc(var(--width)/4);
                width: -moz-calc(var(--width)/4);
                width: -o-calc(var(--width)/4);
                width: -ms-calc(var(--width)/4);
                width: calc(var(--width)/4);
                height: -webkit-calc(var(--width)/8);
                height: -moz-calc(var(--width)/8);
                height: -o-calc(var(--width)/8);
                height: -ms-calc(var(--width)/8);
                height: calc(var(--width)/8);
                border-radius: 50%;
                background-color: var(--loader-color);
                box-shadow: 0 0 10px var(--loader-color),
                            0 0 20px var(--loader-color),
                            0 0 40px var(--loader-color),
                            0 0 60px var(--loader-color),
                            0 0 80px var(--loader-color),
                            0 0 100px var(--loader-color);
                -webkit-animation: animScale var(--duration) linear infinite;
                -moz-animation: animScale var(--duration) linear infinite;
                -o-animation: animScale var(--duration) linear infinite;
                -ms-animation: animScale var(--duration) linear infinite;
                animation: animScale var(--duration) linear infinite;
                animation-delay: -webkit-calc(var(--start-stop)*var(--i));
                animation-delay: -moz-calc(var(--start-stop)*var(--i));
                animation-delay: -o-calc(var(--start-stop)*var(--i));
                animation-delay: -ms-calc(var(--start-stop)*var(--i));
                animation-delay: calc(var(--start-stop)*var(--i));
            }
    }
}
@keyframes animScale
{
    0%
    {
        -webkit-transform: scale(1);
	    -moz-transform: scale(1);
	    -o-transform: scale(1);
	    -ms-transform: scale(1);
        transform: scale(1);
    }
    90%,100%
    {
        -webkit-transform: scale(0);
	    -moz-transform: scale(0);
	    -o-transform: scale(0);
	    -ms-transform: scale(0);
        transform: scale(0);
    }
}
