.content-header-box{
        width: 69%;
        height: auto;
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 4px 1fr;
        grid-template-areas: "one . two";
        .header-box-sup-tittle-one{
            grid-area: one;
        }
        .header-box-sup-tittle-two{
            grid-area: two;
        }
        .header-box-elements{
            width: 100%;
            height: 50px;
            position: relative;
            .header-box-sup-tittle-button{
                background-color: var(--color-siyah);
                box-sizing: border-box;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding-left: 16px;
                border-radius: 16px 16px 0 0;
                font-size: 20px;
                font-weight: 700;
                cursor: pointer;
            }
            @media (max-width:414px){
                .header-box-sup-tittle-button{
                    font-size: 16px;
                }  
            }
            .header-box-sup-tittle-button-selected{
                position: relative;
            }
            .header-box-sup-tittle-button-selected::after{
                content: "";
                position: absolute;
                left: 16px;
                right: 16px;
                bottom: -2px;
                height: 4px;
                background-color: var(--color-beyaz);
                border-radius: 6px;
                z-index: 3;
            }
            .header-box-sub-tittle-box{
                z-index: 2;
                //padding-top: 22px;
                background-color: var(--color-siyah);
                //box-sizing: border-box;
                position: absolute;
                left: 0;
                top: 50px;
                width: 100%;
                height: 0px;
                border-radius: 0 0 16px 16px;
                overflow: hidden;
                -webkit-transition: height .5s linear;
                -moz-transition: height .5s linear;
                -o-transition: height .5s linear;
                -ms-transition: height .5s linear;
                transition: height .5s linear;
                
                    .header-box-sub-tittle{
                    font-size: 16px;
                    font-weight: 300;
                    padding: 16px;
                    color: var(--color-mavi);
                    -webkit-transition: color .3s linear;
                    -moz-transition: color .3s linear;
                    -o-transition: color .3s linear;
                    -ms-transition: color .3s linear;
                    transition: color .3s linear;
                    cursor: pointer;
                    }
                    .header-box-sub-tittle-selected{
                        background-color: var(--color-gri);
                        color: var(--color-beyaz);
                        position: relative;
                    }
                    .header-box-sub-tittle-selected::before{
                        content: "";
                        position: absolute;
                        top: 0px;
                        left: 0;
                        bottom: 0px;
                        width: 16px;
                        background-color: var(--color-siyah);
                        clip-path: polygon(0 0, 0% 100%, 100% 50%);
                    }
                    .header-box-sub-tittle-selected::after{
                        content: "";
                        position: absolute;
                        top: 0px;
                        right: 0;
                        bottom: -1px;
                        width: 16px;
                        background-color: var(--color-siyah);
                        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 100% 50%);
                    }
                
                
            }
            
        }
        .header-box-elements:hover {
            .header-box-sub-tittle-box{
                height: 150px;
            }
        }
}


@media screen and (max-width:960px){
    .content-header-box{
        width: 76%;
    }  
}
@media screen and (max-width:768px){
    .content-header-box{
        width: 84%;
    }  
}
@media screen and (max-width:414px){
    .content-header-box{
        width: 92%;
    }  
}