body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  --color-mavi:#24c7ff;
  --color-koyu-mavi:#070620;
  --color-siyah:#292b2c;
  --color-beyaz:#f7f7f7;
  --color-turuncu:hsl(33, 100%, 50%);
  --color-gri: #3a3a3a;
  --color-pembe:#f72585;
  --color-yesil:#5cb85c;
  --color-kirmizi:#d9534f;
  --font-ma: 'Montserrat Alternates', sans-serif;
  background: var(--color-koyu-mavi) url(/static/media/Rain.5fbebabd.png);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
::-webkit-scrollbar {
  width: 4px;
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(0, 0, 0); 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: darkorange; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: red;
}

@supports (scrollbar-color:red blue){
  *{
    scrollbar-color: darkorange rgb(112, 71, 9);
    scrollbar-width: thin;
  }
}

