


.navbar{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none; 
    -moz-user-select: none; 
    -ms-user-select: none;
    user-select: none;
    .logo{
        background-image: url("../../assets/Logo136x256.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 51px;
        width: 26px;
        margin-left: 2em;
        margin-top: -20px;
        cursor: pointer;
        -webkit-transform: rotate(-90deg);
        -moz-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
        -o-transform: rotate(-90deg);
        transform: rotate(-90deg);
    }
    .navbar-nav{
        width: auto;
        height: auto;
        list-style: none;
        margin-right: 2em;
        display: none;
        justify-content: center;
        align-items: center;
        .nav-item{
            margin-left: 3em;
            width: auto;
            cursor: pointer;
            .nav-link{
                text-decoration: none;
                font-size: 22px;
                color: var(--color-mavi);
                font-family: var(--font-ma);;
                font-weight: 900;
                text-shadow: 0 0 0;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -5px;
                    height: 3px;
                    width: 100%;
                    display: block;
                    background-color: var(--color-mavi);
                    transform: scaleX(0);
                    transform-origin: right;
                    -webkit-transition: transform .5s ease-in;
                    -moz-transition: transform .5s ease-in;
                    -o-transition: transform .5s ease-in;
                    -ms-transition: transform .5s ease-in;
                    transition: transform .5s ease-in;
                }
                &:hover{
                    color: var(--color-mavi);
                    text-shadow: 0 0 50px var(--color-mavi), 0 0 75px var(--color-mavi),  0 0 100px var(--color-mavi),  0 0 125px var(--color-mavi);
                }
                &:hover:before{
                    background-color: var(--color-mavi);
                    transform: scaleX(1);
                    transform-origin: left;
                }
            }
        }
        .selectedItem{
            position: relative;
            width: auto;
            height: auto;
            .nav-link{
                text-decoration: none;
                font-size: 22px;
                color: var(--color-turuncu);
                font-family: var(--font-ma);
                font-weight: 900;
                text-shadow: 0 0 0;
                position: relative;
                cursor: no-drop;
                z-index: 1;
                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -2px;
                    height: 1px;
                    width: 100%;
                    display: block;
                    background-color: var(--color-turuncu);
                    transform: scaleX(1);
                }
                &:hover{
                    color: var(--color-turuncu);
                    text-shadow: 0 0 50px var(--color-turuncu), 0 0 75px var(--color-turuncu),  0 0 100px var(--color-turuncu),  0 0 125px var(--color-turuncu);
                }
                &:hover:before{
                    background-color: var(--color-turuncu);
                    transform: scaleX(1);
                }
                &::after{
                   content: "";
                   position: absolute;
                   top: -100px;
                   left: -8px;
                   min-width: 100%;
                   padding: 0 8px;
                   height: 165px;
                   background-color:var(--color-gri); 
                   z-index: -1;
                   clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 90%, 0 100%);
                }
            }
        }           
    }
}

@media only screen and (min-width:768px){
    .navbar{
        height: 80px;
        .logo{
            -webkit-transform: rotate(0deg);
            -moz-transform: rotate(0deg);
            -ms-transform: rotate(0deg);
            -o-transform: rotate(0deg);
            transform: rotate(0deg);
            margin-top: 2px;
        }
        .navbar-nav{
            display: flex;
        }
    }
}


    
